import React from 'react'

function Faq() {
  return (
    <section id='perguntas-frequentes' className='flex min-h-screen w-full items-center justify-center'>
      <div className='m-4 flex w-11/12 flex-col gap-8 md:m-8 md:w-9/12'>
        <header className='text-center text-4xl md:text-justify md:text-5xl'>Perguntas Frequentes</header>

        <div className='grid grid-cols-1 gap-4 md:grid-cols-2 md:gap-8'>
          <div className='flex flex-col gap-4 rounded border border-gray-300 p-4'>
            <h2 className='text-xl font-medium md:text-2xl'>Como fao para me tornar uma revendedora?</h2>
            <p className='text-base md:text-lg'>
              Para se tornar uma revendedora, voc precisa se cadastrar em nosso site e seguir os passos indicados na
              seo de cadastro. Aps isso, entraremos em contato para orient-la sobre os prximos passos.
            </p>
          </div>
          <div className='flex flex-col gap-4 rounded border border-gray-300 p-4'>
            <h2 className='text-xl font-medium md:text-2xl'>
              Quais so os benefcios de revender os produtos Fani Semijoias?
            </h2>
            <p className='text-base md:text-lg'>
              Ao revender os produtos Fani Semijoias, voc ter acesso a comisses competitivas, treinamento
              especializado, suporte contnuo da equipe e a oportunidade de trabalhar com produtos de alta qualidade
              reconhecidos no mercado.
            </p>
          </div>
          <div className='rounded-lg border border-gray-300 p-6'>
            <h2 className='mb-3 text-xl font-medium md:text-2xl'> necessario algum investimento inicial?</h2>
            <p className='text-base md:text-lg'>
              Nossos produtos so consignados, o que significa que voc no precisa se preocupar com investimentos
              iniciais. Voc pode comear sem custo.
            </p>
          </div>
          <div className='flex flex-col gap-4 rounded border border-gray-300 p-4'>
            <h2 className='text-xl font-medium md:text-2xl'>Qual  a garantia e suporte para produtos com defeitos?</h2>
            <p className='text-base md:text-lg'>
              Oferecemos garantia para todos os nossos produtos. Caso algum produto apresente defeito, voc pode entrar
              em contato com nosso suporte para orientaes sobre a troca ou devoluo. Estamos aqui para garantir a sua
              satisfao e a de seus clientes.
            </p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Faq
