import React from 'react'

function About() {
  return (
    <section id='quem-somos' className='flex min-h-screen items-center justify-center bg-white'>
      <div className='flex w-11/12 flex-col justify-center gap-8 md:w-9/12'>
        <header className='text-center text-4xl md:text-5xl'>Quem Somos</header>

        <div className='flex flex-col gap-6'>
          <div className='flex flex-col gap-4'>
            <header className='text-3xl font-medium md:text-4xl'>Misso</header>
            <p className='text-justify text-xl md:text-3xl'>
              Proporcionar oportunidades excepcionais para que mulheres realizem seus sonhos revendendo Fani Semijoias.
            </p>
          </div>
          <div className='flex flex-col gap-4'>
            <header className='text-3xl font-medium md:text-4xl'>Viso</header>
            <p className='text-justify text-xl md:text-3xl'>
              Ser a empresa lder no mercado de revenda de semijoias no Vale do Iva, oferecendo as melhores
              oportunidades de negcios para mulheres empreendedoras.
            </p>
          </div>
          <div className='flex flex-col gap-4'>
            <header className='text-3xl font-medium md:text-4xl'>Valores</header>
            <p className='text-justify text-xl md:text-3xl'>
              Agir com honestidade em todas as nossas negociaes, capacitando o empoderamento de mulheres de todas as
              origens, levando-as a alcanar sua independncia financeira com semijoias dealtaqualidade.
            </p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default About
